#root{
    padding: 19px;
}
p.help-desk-welcome-msg {
    text-align: right;
    font-weight: 700;
}
.help-desk-welcome-message {
    margin-bottom: 23px;
}
small.error-message { 
    background: #ff7676 !important;    
}
.row.ticket-detail{
    padding: 8px 0px 8px 0px;
    background: #e6e6e6;
    margin: 4px 0px 4px 0px;
}
.has-error input,.has-error select,.has-error textArea{
    border-color:red;
}
.has-error > label:first-child {
    color: red;
}
.has-success input,.has-success select,.has-success textArea{
    border-color:green;
}
.has-success > label:first-child {
    color:green;
}
small.error-message {
    display: block;
    color: #fff;
    background: red;
    font-weight: bold;
    margin: 5px 0px 5px 0px;
    padding: 5px;
}
small.success-message{
    display: block;
    color: #fff;
    background: green;
    font-weight: bold;
    margin: 5px 0px 5px 0px;
    padding: 5px;
}
tr.unreaded{
    font-weight: bold;
}

tr.unreaded td:nth-child(5) span{
    background: red;
    color: #fff;
    padding: 6px;
    border-radius: 14px;
}



